
import Vue from "vue";
import { mapMutations } from "vuex";
import PostList from "./components/PostList/PostList.vue";
import posts from "./posts.json";

export default Vue.extend({
  name: "App",
  components: {
    PostList,
  },
  methods: {
    ...mapMutations(["setPosts"]),
  },
  mounted() {
    this.setPosts(posts.posts);
  },
});
